import { formatDate, isNil } from "../../../../../utils";
import { DATE_FORMAT } from "../constants";
export function normalizeDateRange(range) {
    if (isNil(range)) {
        return null;
    }
    var start = range.start, end = range.end;
    var startDate = new Date(start);
    var endDate = new Date(end);
    var normalizedStartDate = startDate < endDate ? startDate : endDate;
    var normalizedEndDate = startDate < endDate ? endDate : startDate;
    return {
        start: formatDate(normalizedStartDate, DATE_FORMAT),
        end: formatDate(normalizedEndDate, DATE_FORMAT),
    };
}
