import { isNil } from "../../../../../utils";
function transformDateTime(dateTime) {
    if (isNil(dateTime)) {
        return Infinity;
    }
    return dateTime;
}
export function sortTransactions(transactions) {
    return transactions.sort(function (first, second) {
        return transformDateTime(second.operationDateTime) - transformDateTime(first.operationDateTime);
    });
}
