export function createMaxValueValidator(value, message) {
    return {
        type: 'maxValue',
        message: message !== null && message !== void 0 ? message : "\u0417\u043D\u0430\u0447\u0435\u043D\u0438\u0435 \u043D\u0435 \u043C\u043E\u0436\u0435\u0442 \u0431\u044B\u0442\u044C \u0431\u043E\u043B\u044C\u0448\u0435 ".concat(value),
        value: value,
    };
}
