import { requiredValidator } from "../requiredValidator";
export function minLengthValidator(meta) {
    return function (value) {
        var metaValue = Number(meta.value);
        if (!metaValue || isNaN(metaValue)) {
            return true;
        }
        if (meta.type === 'text' || meta.type === 'multiselect') {
            return !requiredValidator(meta)(value) || value.length >= metaValue;
        }
        return true;
    };
}
