import { useEffect } from 'react';
import { useForm } from 'react-final-form';
export function useKeepTouched(id, keepTouched) {
    var form = useForm();
    useEffect(function () {
        if (!keepTouched) {
            form.mutators.clearSavedTouched(id);
        }
    }, [keepTouched]);
    useEffect(function () {
        if (keepTouched) {
            form.mutators.restoreTouched(id);
        }
        return function () {
            // TODO  ,    ,    DOM
            if (keepTouched && form.getFieldState(id)) {
                form.mutators.saveTouched(id, form.getFieldState(id).touched);
            }
            else {
                form.mutators.clearSavedTouched(id);
            }
        };
    }, []);
}
