import { concatStr, requiredField, uniqueField } from "./";
import { WEB_TRANSACTION_ID } from "../WebProductsTransactions";
export function createTransactionField(selectedProduct, index) {
    return {
        id: concatStr(WEB_TRANSACTION_ID, selectedProduct, index.toString()),
        value: '',
        type: 'select',
        validators: [requiredField(), uniqueField()],
    };
}
