import cn from 'classnames';
import { isNil } from "../../../utils/isNil";
import globalStyles from 'assets/scss/common/globalCssClass.module.scss';
var GLOBAL_CLASSNAME = globalStyles.wf;
export function withGlobalStyles(className) {
    return !isNil(className) && className.includes(GLOBAL_CLASSNAME)
        ? className
        : cn(className, GLOBAL_CLASSNAME);
}
