import { checkStrategyCondition } from "../checkStrategyCondition";
/**
 *     S-,       .
 *           .
 *        .
 * ,   ,   .
 *
 * @example
 * checkConditionIsTrue([
 *   'and',
 *   {
 *     id: 'input',
 *     value: '^test$'
 *   },
 *   [
 *     'or',
 *     {
 *       id: 'multiselect'
 *       value: '^moscow$',
 *     },
 *     {
 *       id: 'checkbox',
 *       value: '^checked$',
 *     }
 *   ]
 * ],
 * {
 *   input: 'test',
 *   multiselect: ['moscow', 'st. petersburg'],
 *   checkbox: 'not checked'
 * }) === true
 *
 * @param condition
 * @param values
 *
 * @return boolean
 */
export function checkStrategyConditions(condition, values) {
    var currentOperator = null;
    if (!condition) {
        return true;
    }
    return condition.reduce(function (accum, item) {
        if (item === 'and' || item === 'or') {
            currentOperator = item;
            return accum;
        }
        if (Array.isArray(item)) {
            return currentOperator === 'and'
                ? accum && checkStrategyConditions(item, values)
                : accum || checkStrategyConditions(item, values);
        }
        if (currentOperator === 'and' && accum !== null) {
            return accum && checkStrategyCondition(item, values);
        }
        else if (currentOperator === 'or' && accum !== null) {
            return accum || checkStrategyCondition(item, values);
        }
        return checkStrategyCondition(item, values);
    }, null);
}
