export function createHOC(displayName, hocFn) {
    return function (component) {
        function ComponentWithHOC(props) {
            return hocFn(component, props);
        }
        var wrappedComponentName = component.displayName || component.name || 'Component';
        ComponentWithHOC.displayName = "".concat(displayName, "(").concat(wrappedComponentName, ")");
        return ComponentWithHOC;
    };
}
