import { add, sub } from 'date-fns';
import { formatDate, isNil } from "../../../../../utils";
import { DATE_FORMAT } from "../constants";
export function getInitialDateRange(date, minDate, maxDate) {
    var hasMinMaxError = !isNil(minDate) && !isNil(maxDate) && new Date(minDate) > new Date(maxDate);
    if (isNil(date) || hasMinMaxError) {
        return null;
    }
    var start = sub(new Date(date), { weeks: 1 });
    var end = add(new Date(date), { weeks: 1 });
    if (!isNil(minDate) && new Date(minDate) > start) {
        start = new Date(minDate);
    }
    if (!isNil(maxDate) && new Date(maxDate) < end) {
        end = new Date(maxDate);
    }
    return {
        start: formatDate(start, DATE_FORMAT),
        end: formatDate(end, DATE_FORMAT),
    };
}
