export function createMinLengthValidator(value, message) {
    return {
        type: 'minLength',
        message: message !== null && message !== void 0 ? message : "\u041A\u043E\u043B\u0438\u0447\u0435\u0441\u0442\u0432\u043E \u0441\u0438\u043C\u0432\u043E\u043B\u043E\u0432 \u043D\u0435 \u043C\u043E\u0436\u0435\u0442 \u0431\u044B\u0442\u044C \u043C\u0435\u043D\u044C\u0448\u0435 ".concat(value),
        value: value,
    };
}
