import { formatCurrency, formatDate } from "../../../../../utils";
import { DISPLAY_DATE_FORMAT } from "../constants";
export function formatTransactionValue(value, format) {
    if (format === 'date') {
        return formatDate(value, DISPLAY_DATE_FORMAT);
    }
    if (format === 'dateTime') {
        return formatDate(value, "".concat(DISPLAY_DATE_FORMAT, " HH:mm"));
    }
    if (format === 'currency') {
        return formatCurrency(value);
    }
    return value;
}
