import { useEffect } from 'react';
import { useForm } from 'react-final-form';
import { noop } from 'lodash';
import { validateSelectedTransactions } from "../utils";
export function useSyncSelectedTransactionsWithForm(id, selectedTransactions) {
    var form = useForm();
    useEffect(function () {
        form.change(id, selectedTransactions);
        return function () {
            form.change(id, undefined);
        };
    }, [id, selectedTransactions]);
    useEffect(function () {
        return form.registerField(id, noop, {}, { silent: true, getValidator: function () { return validateSelectedTransactions; } });
    }, [id]);
}
